body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(2, 29, 44);
  color:#ffffff;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login-page{
    width: 100vw;
    height: 100vh;
    background-color: #002144;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-content: center;
            align-content: center;
    background-image: url(/static/media/back.2c10a7a7.png);
    background-position: 0% 27% ;

}


.login-form-cont{
    position: fixed;
    -webkit-align-content: center;
            align-content: center;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: fixed;
    right: 0px;
    top: 0px;
    height: 100vh;
    width: 20vw;
    /* background-color: black; */
    background-image: -webkit-linear-gradient(left, rgba(3, 47, 73, 0.87) ,rgb(3, 47, 73), rgb(3, 47, 73));
    background-image: linear-gradient(to right, rgba(3, 47, 73, 0.87) ,rgb(3, 47, 73), rgb(3, 47, 73));
    color:white;
    padding-top: 30vh;
}
  
.login-img{
    width: 32px;
    height: 32px;
    margin-left: 5px;
}

.login-btn{
    padding: 10px;
    background: #fff;
    border-radius: 3px;
    color: #6546a4;
    font-weight: bold;
    font-size: 16px;
    
}
.first-page{
    -webkit-align-content: center;
            align-content: center;
    text-align: center;
    padding-top: 20vh;
}

.form-a{
 margin-top: 10vh;
}

.events{
    padding: 15px;
    padding-left: 25px;
    background-color: rgb(13, 43, 59);
    font-size: 14px;
    color:#ffffff;
    
}

.nav{
    background-color: #320c7e;
    margin: 0px;
    top:0px;
    padding:25px;
}

hr{
    background: #aaa;
}

